<template>
    <div>
        <el-main>
            <div class="select">
                <el-input v-model="gid" placeholder="账号" clearable></el-input>
                <el-input v-model="name" placeholder="姓名" clearable></el-input>
            </div>
            <!-- <div class="btn">
                <el-button type="primary" icon="el-icon-plus" @click="add()">添加</el-button>
            </div> -->
            <el-table
                border
                ref="multipleTable"
                tooltip-effect="dark"
                style="width: 100%"
                :header-cell-style="{background:'#f5f7fa'}"
                :default-sort="{prop: 'gid'}"
                :data="tableData"
            >
                <el-table-column prop="gid" label="账号" sortable></el-table-column>
                <el-table-column prop="name" label="姓名"></el-table-column>
                <el-table-column prop="role" label="角色"></el-table-column>
                <el-table-column
                    prop="state"
                    label="状态"
                    min-width="100"
                >
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.state === 1||scope.row.state === true ? 'primary' : 'warning'"
                            disable-transitions
                        >{{scope.row.state==1||scope.row.state === true?'已启用':'已停用'}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                style="float: right;margin-top:8px;"
                background
                :current-page.sync="currentPage"
                @current-change="handleCurrentChange"
                layout="prev, pager, next"
                :page-size="7"
                :total="totalPage"
            ></el-pagination>
        </el-main>
        <pm :pmitem="pmitem" :pmVisible="pmVisible" @close-dialogStatus="Close_dialog"></pm>
        <detail
            :detailVisible="detailVisible"
            @close-dialogStatus="Close_dialog"
        ></detail>
    </div>
</template>

<script>
import pm from "@/components/dialog/pm";
import detail from "@/components/dialog/detail";
import axios from "axios";
export default {
    data() {
        return {
            pmitem: {},
            pmVisible: false,
            detailVisible: false,
            gid:"",
            name: "",
            roles: "",
            currentPage: 1,
            totalPage: 1,
            tableData: []
        };
    },
    components: {
        pm,
        detail
    },
    created: function() {
        this.getList();
    },
    watch: {
        gid(newValue, oldValue) {
            this.getList();
        },
        name(newValue, oldValue) {
            this.getList();
        },
    },
    methods: {
        getList() {
            let here = this;
            axios({
                url: "/gllist",
                params: {
                    gid: here.gid,
                    name: here.name,
                    currentPage: here.currentPage
                }
            })
                .then(function(response) {
                    let data = response.data;
                    here.tableData = data.data;
                    here.totalPage = data.totalPage;
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        Close_dialog(val) {
            this.pmVisible = false;
            this.detailVisible = false;
            this.getList();
        },
        handleEdit(index, row) {
            this.pmitem = row;
            this.pmVisible = true;
        },
        add() {
            this.detailVisible = true;
        },
        handleCurrentChange() {
            this.getList();
        },
    }
};
</script>

<style lang="stylus" scoped>
body {
    margin: 0;
}

.select, .el-input {
    width: 156px;
    margin-bottom 10px;
}
.select {
    display: inline;
    margin-bottom: 10px;
}
.btn {
    margin-top: 10px;
    margin-bottom: 10px;
}
.search {
    position: relative;
    top: -1px;
}
</style>


