<template>
    <div>
        <el-dialog title="编辑" :visible.sync="dialogpmisible" @close="closeDialog" width="30%">
            <el-form ref="form" :model="form">
                <el-form-item label="角色" prop="role" label-width="40px">
                    <el-checkbox-group v-model="form.role">
                        <el-checkbox v-for="r in roleLists" :key="r.rid" :label="r.name" name="role"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item label="状态">
                    <el-switch v-model="form.state"></el-switch>
                </el-form-item>
                <el-form-item style="text-align: right;">
                    <el-button type="primary" @click="onSubmit">提交</el-button>
                    <el-button @click="dialogpmisible=false;">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
import axios from "axios";
export default {
    data() {
        return {
            form: {},
            roleLists: [],
            dialogpmisible: false
        };
    },
    props: {
        pmVisible: Boolean,
        pmitem: Object
    },
    created: function() {
        this.getList();
    },
    watch: {
        pmVisible(newValue, oldValue) {
            this.dialogpmisible = newValue;
        },
        pmitem(newValue, oldValue) {
            this.form = newValue;
            console.log(this.form);
            if(newValue.state===1) this.form.state = true;
            else this.form.state = false;
        }
    },
    methods: {
        getList() {
            let here = this;
            axios({
                url: "/roleLists"
            })
                .then(function(response) {
                    let data = response.data;
                    here.roleLists = data;
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        closeDialog() {
            this.$emit("close-dialogStatus", true);
        },
        onSubmit() {
            let here = this;
            axios({
                method:"post",
                url: "/gledit",
                data: here.form
            })
                .then(function(response) {
                    if (response.data === 1) {
                        here.$message({
                            message: "修改成功",
                            type: "success"
                        });
                    }else{
                        here.$message.error("操作失败!");
                    }
                    here.dialogpmisible = false;
                })
                .catch(function(error) {
                    console.log(error);
                });
        }
    }
};
</script>


