<template>
    <div>
        <el-dialog title="添加" :visible.sync="dialogdetailisible" @close="closeDialog" width="40%">
            <el-form ref="form" :model="form" label-width="100px" :rules="rules">
                <el-form-item label="账号" prop="id">
                    <el-input v-model="form.id" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="姓名" prop="name">
                    <el-input v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="角色" prop="role">
                    <el-checkbox-group v-model="form.role">
                        <el-checkbox v-for="r in roleLists" :key="r.rid" :label="r.name" name="role"></el-checkbox>
                        <el-checkbox label="超级管理员" name="role"></el-checkbox>
                        <el-checkbox label="部门1管理员" name="role"></el-checkbox>
                        <el-checkbox label="部门2管理员" name="role"></el-checkbox>
                        <el-checkbox label="部门3管理员" name="role"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item label="状态">
                    <el-switch v-model="form.state"></el-switch>
                </el-form-item>
                <el-form-item style="text-align: right;">
                    <el-button type="primary" @click="onSubmit">提交</el-button>
                    <el-button @click="dialogdetailisible=false;">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
import axios from "axios";
export default {
    data() {
        var validateId = (rule, value, callback) => {
            if (!value) {
                return callback(new Error("请输入管理员账号"));
            }
            let id = value;
            var format = /^[0-9]/;
            //号码规则校验
            if (!format.test(id)) {
                return callback(new Error("只能输入数字"));
            }
            callback();
        };
        return {
            dialogdetailisible: false,
            roleLists:[],
            form: { role: [] },
            rules: {
                id: [
                    {
                        required: true,
                        validator: validateId,
                        trigger: "blur"
                    }
                ],
                name: [
                    {
                        required: true,
                        message: "请输入管理员姓名",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    props: {
        detailVisible: Boolean
    },
    created: function() {
        this.form = { role: [] };
        this.getList();
    },
    watch: {
        detailVisible(newValue, oldValue) {
            this.dialogdetailisible = newValue;
        }
    },
    methods: {
        getList() {
            let here = this;
            axios({
                url: "/roleLists"
            })
                .then(function(response) {
                    let data = response.data;
                    here.roleLists = data;
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        closeDialog() {
            this.$emit("close-dialogStatus", true);
        },
        onSubmit() {
            let here = this;
            axios({
                url: "/gledit"
            })
                .then(function(response) {
                    if (response.data.success === 1) {
                        here.$message({
                            message: "修改成功",
                            type: "success"
                        });
                    }else{
                        here.$message.error("操作失败!");
                    }
                    here.dialogpmisible = false;
                })
                .catch(function(error) {
                    console.log(error);
                });
        }
    }
};
</script>

<style lang="stylus" scoped></style>


